<template>
 <router-view v-if="$route.path!=='/oilCardFile'"></router-view>

  <div class="app-container" id="oilCarFile" v-else>
    <div class="filter-container">
      <el-input
        v-model="listQuery.iMei"
        placeholder="车牌号/油卡号"
        class="filter-item"
        @keyup.enter.native="handleFilter"
      />
      <el-button class="filter-item" type="primary"  @click="handleFilter" icon="el-icon-search">搜索</el-button>
      <el-button
        class="filter-item"
        type="primary"
        @click="handleCreate"
        v-if="power==1"
        icon="el-icon-circle-plus-outline"
      >添加</el-button>
       <el-button class="filter-item filter-item-btn" type="primary" @click="exportExcel" icon="iconfont icondaochu">导出</el-button>
    </div>
<div class="tableBox">

    <el-table
      :key="tableKey"
      v-loading="listLoading"
      :data="list"
      fit
      highlight-current-row
      style="width: 100%;"
       :row-class-name="tableRowClassName"
    >
      <el-table-column
        label="序号"
        fixed
        prop="id"
        sortable="custom"
        align="center"
        width="80"
        type="index"
        :index="indexMethod"
      ></el-table-column>
      <el-table-column label="id" width="100px" align="center">
        <template slot-scope="{row}">
          <span>{{ row.id }}</span>
        </template>
      </el-table-column>
      <el-table-column label="油卡号" width="200px" align="center">
        <template slot-scope="{row}">
          <span class="link-type" v-if="power==1" @click="handleUpdate(row)">{{ row.fuelcardNo }}</span>
          <span v-else>{{ row.fuelcardNo }}</span>
        </template>
      </el-table-column>
      <el-table-column label="车牌号" minWidth="200px" align="center">
        <template slot-scope="{row}">
          <span>{{ row.licenseNo }}</span>
        </template>
      </el-table-column>
      <el-table-column label="最后一次加油量" minWidth="200px" align="center">
        <template slot-scope="{row}">
          <span>{{ row.latestRefuel }}</span>
        </template>
      </el-table-column>
      <el-table-column label="最后一次加油金额" minWidth="200px" align="center">
        <template slot-scope="{row}">
          <span>{{ row.latestPrice }}</span>
        </template>
      </el-table-column>
      <el-table-column label="最后一次加油时间" minWidth="200px" align="center">
        <template slot-scope="{row}">
          <span>{{ formatDateTime(row.latestTime) }}</span>
        </template>
      </el-table-column>
      <el-table-column label="累计加油量" minWidth="200px" align="center">
        <template slot-scope="{row}">
          <span>{{ row.totalRefuel }}</span>
        </template>
      </el-table-column>
      <el-table-column label="累计加油金额" minWidth="200px" align="center">
        <template slot-scope="{row}">
          <span>{{ row.totalPrice }}</span>
        </template>
      </el-table-column>
      <el-table-column label="油卡创建时间" minWidth="200px" align="center">
        <template slot-scope="{row}">
          <span>{{ formatDateTime(row.createTime) }}</span>
        </template>
      </el-table-column>
      <el-table-column label="所属大队" minWidth="200px" align="center">
        <template slot-scope="{row}">
          <span>{{ row.teamName==""?'未分组':row.teamName }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="操作"
        fixed="right"
        align="center"
        width="350"
        class-name="small-padding fixed-width"
        v-if="power==1"
      >
        <template slot-scope="scope">
          <el-button type="primary" size="mini" class="pausebtn" @click="check(scope.row)">加油记录</el-button>
          <el-button type="primary" size="mini" @click="handleUpdate(scope.row)" v-if="power==1" icon="el-icon-edit-outline">编辑</el-button>
          <el-button type="danger" size="mini" @click="handleDelete(scope.row)" v-if="power==1" icon="el-icon-delete">删除</el-button>
         <!-- <el-button type="primary" size="mini" icon="el-icon-more"></el-button> -->
         <el-popover
          placement="left"
          trigger="click"
          :ref="`popover-${scope.row.id}`"
          >
          <div style="text-align: center; margin: 0">
              <el-button size="mini" type="primary" v-if="scope.row.carId==-1" @click="handleBind(scope.row)">绑定车辆</el-button>
              <el-button type="warning" size="mini" v-else  @click="handleUnbind(scope.row)">解绑车辆</el-button>
          </div>
          <el-button size="mini" class="moreBtn" title="更多操作" icon="el-icon-more" slot="reference" circle></el-button>
        </el-popover>
        </template>
      </el-table-column>
    </el-table>
</div>

    <pagination
      v-show="total>0"
      :total="total"
      :page.sync="listQuery.page"
      :limit.sync="listQuery.limit"
      @pagination="getList"
      layout="prev, pager, next, sizes, total, jumper"
    />

    <el-dialog id="myDialog" :title="textMap[dialogStatus]" :visible.sync="dialogFormVisible" center > 
      <el-form
        ref="dataForm"
        :rules="rules"
        :model="temp"
        label-position="left"
        class="formStyle"
      >
        <el-form-item label="油卡号" prop="fuelcardNo">
          <el-input v-model="temp.fuelcardNo" />
        </el-form-item>
        <!-- <el-form-item label="车牌号" prop="licenseNo">
          <el-input v-model="temp.licenseNo" />
        </el-form-item> -->
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogStatus==='create'?createData():updateData()">确定</el-button>
      </div>
    </el-dialog>
     <el-dialog id="myDialog" @close="scrollTop" title="绑定车辆" :visible.sync="dialogVisibleDev" center>
      <el-form
        ref="bindForm"
        :rules="rules2"
        label-position="left"
        class="bindForm formStyle"
        :model="bindTemp"
      >   
  <el-form-item prop="imeiCar">
  <el-cascader
              v-model="imeiCar"
              placeholder="绑定车辆"
              :options="dataCar"
              :show-all-levels="false"
              clearable
              filterable
              collapse-tags
            >
            </el-cascader>
  </el-form-item>
      </el-form>
<div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="bind()">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Pagination from "../../../components/Pagination"; // secondary package based on el-pagination
//油卡列表，添加车组，编辑，删除，搜索
import {
  oilCarList,
  oilCarAdd,
  oilCarUpdate,
  oilCarDelete,
  oilCarSearch,
  oilCarBind,
  oilCarUnbind
} from "../../../api/managementArchives/oilManagement";
import {
  teamAndCarList
  
} from "../../../api/realTimeMonitoring";
import { formatDate } from "../../../../src/common";
import {
 
  exportExcelReq
} from "../../../api/managementArchives/common";
let myLoading = undefined; // 弹窗服务调用

export default {
  name: "gunFiles",
  components: { Pagination },
  data() {
      let multiRule = (rule, value, callback) => {
      // 多设备验证
      let cars = this.imeiCar
      if (cars.length < 1) {
        callback(new Error("请选择设备"));
      } else {
        callback();
      }
    };
    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 权限
      tableKey: 0,
      list: null, //油卡列表
      total: 0, //油卡总数
      indexMethod: 1, // 序号
      listLoading: false,
      listQuery: {
        page: 1,
        limit: 10,
        iMei: undefined
      },
      //油卡添加或编辑提交的数据
      temp: {
      
        fuelcardNo: undefined
        
      },
      dialogFormVisible: false, //弹窗显隐
      dialogVisibleDev: false, //绑定

      dialogStatus: "", //控制是编辑窗口还是添加窗口
      textMap: {
        update: "编辑",
        create: "添加"
      },
      //添加或编辑表单校验
      rules: {
        fuelcardNo: [
          { required: true, message: "请填写油卡号", trigger: "blur" }
        ],
        licenseNo: [
          { required: true, message: "请填写车牌号", trigger: "blur" }
        ]
      },
       rules2: {
        imeiCar: [{ validator: multiRule, trigger: "change" }],
      },
      dataCar: [], // 车辆列表
       imeiCar:[],
      imei2name: {}, // 设备的imei和对应的名字
      bindTemp:{},
      cardId:undefined
    };
  },
  created() {
    this.getTeamAndCarList();
    this.getList(); //油卡列表获取
  },
  methods: {
     //获取左侧大队和车辆tree请求
    getTeamAndCarList() {
      let that = this;
      teamAndCarList({}).then(res => {
        if (res.status == 0) {
          that.dataCar = this.formatCas(res.data, "id", "licenseNo"); // 设置车下拉树的数据格式
          // console.warn(this.dataCar);
        } else {
          this.$message.error(res.msg);
        }
      });
    },
     formatCas(data, imei, name) {
      // 格式化级联选择器数据 data是原始数据 imei是vaue值 name是label值
      let result = [],
        id = 0,
        _t = this;
      for (let i in data) {
        if(i=="报废车辆"){continue}
        let childs = []; // 格式化子数组
        data[i].forEach(x => {
        //   if (this.pausedDevice.hasOwnProperty(x[imei])) {
        //     disabled = true;
        //     time = this.pausedDevice[x[imei]];
        //   }

        //   let disabled2 = disabled;
        //   //如果不是暂停或取消拆卸报警,都是不禁用的
        //   if (this.pauseType == 1) {
        //     disabled2 = false;
        //   } else {
        //     //如果是暂停或取消拆卸报警,判断是禁用暂停的还禁用没暂停的设备
        //     if (this.pauseVal == 2) {
        //       disabled2 = !disabled; 
        //     }
        //   }
          childs.push({
            value: x[imei],
            label: x[name]
          });
          _t.imei2name[x[imei]] = x[name]; // 将设备号与设备名组成一个数组
        });
        result.push({
          value: `i${id++}`,
          label: i == "" ? "未分组" : i,
          children: childs
        });
      }
      return result;
    },
     scrollTop() {
      // 弹窗滚动到顶部
      let myDialog = document.querySelector("#myDialog .el-dialog__body");
      myDialog.scrollTop = 0;
    },
    //格式化时间
    formatDateTime(timestamp) {
      //下面第二个参数传1代表时间会显示出时分秒，不传或者其他的都只显示年月日
      return formatDate(timestamp, 1);
    },
    //获取 油卡列表
    getList() {
      this.listLoading = true;
      oilCarList({
        page: this.listQuery.page,
        limit: this.listQuery.limit
      })
        .then(res => {
          if (res.status == 0) {
            this.listLoading = false;
            if (res.data == null) {
              this.list = [];
            } else {
              this.list = res.data;
              for(let i=0;i<this.list.length;i++){
               this.list[i].visible=false;
              }
            }
            this.total = res.count;
            this.indexMethod =
              (this.listQuery.page - 1) * this.listQuery.limit + 1;
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
          this.listLoading = false;
        });
    },
//点击绑定
    handleBind(row) {
      
      this.dialogVisibleDev = true;
      this.imeiCar=[];
    //      let teamId=row.teamId;
    //      let teamName;
    //       if(teamId!==-1){
    //       teamName=row.teamName
    //      }
    //   for(let i=0;i<this.dataCar.length;i++){
        
    //      if(teamName==  this.dataCar[i].label ){
    //         //  console.warn(teamName);
    //         //  console.warn(row.carId);   
    //      this.imeiCar=[this.dataCar[i].value,row.carId];
    //      break;
    //      }
    //   }
      this.cardId=row.id;
    
    },
    //绑定
    bind() {
      this.$refs["bindForm"].validate(valid => {
          // console.warn(this.imeiCar)
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          let carId="";
          if(this.imeiCar.length){
           carId= this.imeiCar[1]  
          }else{
            carId=-1;
          }
          oilCarBind({cardId:this.cardId,
          carId:carId
          })
            .then(res => {
              if (res.status == 0) {
                // this.listQuery.page = 1;
                this.getList();
                this.$message.success("绑定成功");
                this.dialogVisibleDev = false;
                //实时监控检测到变化重新获取车辆和枪支车牌号或枪支号和imei号放入carAndGunObj，及时更新信息
                // this.$store.commit("realTimeMonitor/ISEDITFILES", true);
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        } else {
          return false;
        }
      });
    },
    //点击搜索
    handleFilter() {
      //如果搜索内容为空则显示所有油卡
      if (this.listQuery.iMei == "" || this.listQuery.iMei == undefined) {
        this.listQuery.iMei = null;
        this.getList();
        return;
      }
      oilCarSearch({
        keyword: this.listQuery.iMei
      }).then(res => {
        if (res.status == 0) {
          this.listQuery.page = 1;
          this.listLoading = false;
          this.list = [];
          this.list = res.data;
          this.total = 0;
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    //重置
    resetTemp() {
      this.temp = {
       
        fuelcardNo: undefined
       
      };
    },
    //点击增加
    handleCreate() {
      this.resetTemp();
      this.dialogStatus = "create";
      this.dialogFormVisible = true;

      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },

    //增加油卡
    createData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          oilCarAdd({
            fuelcardNo: this.temp.fuelcardNo
           
          })
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success(res.msg);
                this.dialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        } else {
          return false;
        }
      });
    },
    //点击编辑
    handleUpdate(row) {
      this.temp = Object.assign({}, row); // copy obj
      this.dialogStatus = "update";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },
    //编辑油卡
    updateData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          oilCarUpdate({
            id: this.temp.id,
            fuelcardNo: this.temp.fuelcardNo
           
          })
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success(res.msg);
                this.dialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        } else {
          return false;
        }
      });
    },
    handleUnbind(row){
      let _t=this;
     oilCarUnbind({
        cardId: row.id
      })
        .then(res => {
          if (res.status == 0) {
            if (_t.listQuery.page > 1 && _t.list.length <= 1) {
              // 最后一条返回上一页
              _t.listQuery.page -= 1;
            }
            this.getList();
            _t.$message.success(res.msg);
          } else {
            this.$message.error(res.msg);
            _t.listLoading = false;
          }
        })
        .catch(err => {
          _t.listLoading = false;
        });
    },
    //删除油卡
    handleDelete(row) {
      let _t = this;
      _t.listLoading = true;
      oilCarDelete({
        id: row.id
      })
        .then(res => {
          if (res.status == 0) {
            if (_t.listQuery.page > 1 && _t.list.length <= 1) {
              // 最后一条返回上一页
              _t.listQuery.page -= 1;
            }
            this.getList();
            _t.$message.success("刪除成功");
          } else {
            this.$message.error(res.msg);
            _t.listLoading = false;
          }
        })
        .catch(err => {
          _t.listLoading = false;
        });
    },

    //点击查看加油记录
    check(row) {
      this.$router.push({
        name: "oilRecord",
        params: { fuelcardNo: row.fuelcardNo, licenseNo: row.licenseNo }
      });
    },
     tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    },
     exportExcel() {
      let _t = this;
      // 查找数据
      exportExcelReq({ excelType: 12,title:'油卡信息' }).then(res => {
         try {
              let enc = new TextDecoder('utf-8')
              let data = JSON.parse(enc.decode(new Uint8Array(res)))
              this.$message({
                message: data.msg,
                type: 'warning'
              });

            } catch (error) {
        let disposition = res.headers;
        let fileName = "油卡信息" + ".xls";
        let blob = new Blob([res], { type: "application/excel" }); // 假设文件为pdf
        let link = document.createElement("a");
        link.href = window.URL.createObjectURL(blob);
        link.download = fileName;
        link.click();
        link.remove();
            }
      });
    }
  }
};
</script>
<style lang="scss">
#oilCarFile {
 .pausebtn{
   width:92px;
   padding-left:10px;
 }
 .left .el-form-item__error {
    top: 96%!important;
}
 .moreBtn{
   width:auto;
   height:auto;
   padding:6px;
   margin-left:10px;
 }
}
</style>




